<!-- src/views/StoreView.vue -->
<template>
  <v-container fluid class="store-view py-10">
    <v-row justify="center">
      <v-col cols="12" md="10" lg="9">
        <!-- Error -->
        <v-alert
          v-if="error"
          type="error"
          variant="tonal"
          border="start"
          density="comfortable"
          class="mb-4"
        >
          {{ error }}
        </v-alert>

        <!-- Loading -->
        <v-sheet
          v-if="loading"
          class="d-flex flex-column align-center justify-center py-16"
          color="transparent"
          elevation="0"
        >
          <v-progress-circular indeterminate size="34" class="mb-3" />
          <span class="text-body-2 text-medium-emphasis">
            Cargando tiendas...
          </span>
        </v-sheet>

        <template v-else>
          <!-- Sin ?store: elegir tienda -->
          <div v-if="!storeParam">
            <!-- Hero superior -->
            <v-sheet
              class="header-card mb-8"
              rounded="xl"
              elevation="0"
            >
              <v-row no-gutters align="center">
                <v-col cols="12" md="8">
                  <h1 class="text-h4 font-weight-bold mb-2">
                    Multi tienda moderna
                  </h1>
                  <p class="text-body-2 text-medium-emphasis mb-4">
                    Elige una tienda para explorar productos curados por categoría.
                  </p>

                  <div class="d-flex flex-wrap align-center ga-2 text-caption text-medium-emphasis">
                    <span>También puedes usar la URL:</span>
                    <v-chip size="small" variant="outlined">
                      ?store=tech
                    </v-chip>
                    <v-chip size="small" variant="outlined">
                      ?store=fashion
                    </v-chip>
                  </div>
                </v-col>

                <v-col
                  cols="12"
                  md="4"
                  class="d-flex justify-end"
                >
                  <div class="text-right d-none d-md-block">
                    <div class="text-caption text-medium-emphasis mb-1">
                      Tiendas disponibles
                    </div>
                    <div class="text-h5 font-weight-semibold">
                      {{ stores.length }}
                    </div>
                  </div>
                </v-col>
              </v-row>
            </v-sheet>

            <!-- Grid de tiendas -->
            <v-row dense>
              <v-col
                v-for="store in stores"
                :key="store.slug"
                cols="12"
                sm="6"
                md="4"
              >
                <v-card
                  class="store-card"
                  rounded="xl"
                  elevation="1"
                  @click="goToStore(store.slug)"
                >
                  <v-card-text class="pa-5">
                    <StoreHeader :store="store" />
                  </v-card-text>

                  <v-divider class="mx-4" />

                  <v-card-actions class="px-4 py-3 d-flex justify-space-between align-center">
                    <span class="text-caption text-medium-emphasis">
                      Ver catálogo
                    </span>
                    <v-btn
                      variant="flat"
                      color="primary"
                      size="small"
                      class="text-none"
                      append-icon="mdi-arrow-right"
                    >
                      Abrir
                    </v-btn>
                  </v-card-actions>
                </v-card>
              </v-col>
            </v-row>
          </div>

          <!-- Con ?store: vista de tienda -->
          <div v-else>
            <v-btn
              variant="text"
              color="primary"
              prepend-icon="mdi-arrow-left"
              class="mb-4 text-none px-0"
              @click="clearStore"
            >
              Volver a las tiendas
            </v-btn>

            <div v-if="currentStore">
              <!-- Hero de la tienda -->
              <v-sheet
                class="store-hero mb-6"
                rounded="xl"
                elevation="0"
              >
                <v-card-text class="pa-5 pa-md-6">
                  <StoreHeader
                    :store="currentStore"
                    :show-description="true"
                  />
                </v-card-text>
              </v-sheet>

              <div class="d-flex align-center justify-space-between mb-2 products-header">
                <h2 class="text-h5 font-weight-medium mb-0">
                  Productos
                </h2>

                <v-chip
                  size="small"
                  color="primary"
                  variant="tonal"
                >
                  {{ currentStore.products.length }} artículo(s)
                </v-chip>
              </div>

              <v-divider class="mb-4" />

              <v-row dense>
                <v-col
                  v-for="product in currentStore.products"
                  :key="product.id"
                  cols="12"
                  sm="6"
                  md="4"
                  lg="3"
                >
                  <ProductCard :product="product" />
                </v-col>
              </v-row>
            </div>

            <v-alert
              v-else
              type="warning"
              variant="tonal"
              border="start"
              density="comfortable"
              class="mt-6"
            >
              No se encontró la tienda
              <strong class="mx-1">{{ storeParam }}</strong>.
              Elige una tienda válida.
            </v-alert>
          </div>
        </template>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { mapState, mapGetters, mapActions } from "vuex";
import StoreHeader from "@/components/StoreHeader.vue";
import ProductCard from "@/components/ProductCard.vue";

export default {
  name: "StoreView",
  components: {
    StoreHeader,
    ProductCard,
  },
  computed: {
    ...mapState("stores", ["stores", "currentStore", "loading", "error"]),
    ...mapGetters("stores", ["hasStores"]),
    storeParam() {
      return this.$route.query.store || null;
    },
  },
  watch: {
    // Cuando cambie ?store, volvemos a seleccionar
    storeParam: {
      immediate: true,
      handler(newValue) {
        if (!this.hasStores) return;
        if (newValue) {
          this.selectStoreBySlug(newValue);
          this.updateTitle();
        } else {
          this.$store.commit("stores/SET_CURRENT_STORE", null);
          this.$router.currentRoute.value.meta.title = "Multi Tienda Vue";
          document.title = "Multi Tienda Vue";
        }
      },
    },
    currentStore(store) {
      // Actualizamos meta title cuando se cargue la tienda
      if (store) {
        this.updateTitle();
      }
    },
  },
  created() {
    // Cargamos tiendas al entrar (si no estaban ya)
    if (!this.hasStores) {
      this.fetchStores().then(() => {
        if (this.storeParam) {
          this.selectStoreBySlug(this.storeParam);
          this.updateTitle();
        }
      });
    } else if (this.storeParam) {
      this.selectStoreBySlug(this.storeParam);
      this.updateTitle();
    }
  },
  methods: {
    ...mapActions("stores", ["fetchStores", "selectStoreBySlug"]),
    goToStore(slug) {
      this.$router.push({ name: "Store", query: { store: slug } });
    },
    clearStore() {
      this.$router.push({ name: "Store", query: {} });
    },
    updateTitle() {
      if (!this.currentStore) return;
      const base = "Multi Tienda Vue";
      const title = `${this.currentStore.name} - ${base}`;
      this.$router.currentRoute.value.meta.title = title;
      document.title = title;
    },
  },
};
</script>

<style scoped>
.store-view {
  min-height: 100vh;
  background-color: #f3f4f6; /* gris claro tipo dashboard */
}

/* Hero de arriba (selector de tienda) */
.header-card {
  background-color: #ffffff;
  border-radius: 24px;
  border: 1px solid #e5e7eb;
}

/* Card de selección de tienda */
.store-card {
  background-color: #ffffff;
  border-radius: 22px;
  border: 1px solid #e5e7eb;
  transition:
    transform 0.15s ease,
    box-shadow 0.15s ease,
    border-color 0.15s ease;
}

.store-card:hover {
  transform: translateY(-4px);
  box-shadow: 0 18px 40px rgba(15, 23, 42, 0.12);
  border-color: rgba(59, 130, 246, 0.4);
}

.store-card:active {
  transform: translateY(-1px);
  box-shadow: 0 8px 24px rgba(15, 23, 42, 0.18);
}

/* Hero de tienda seleccionada */
.store-hero {
  background: linear-gradient(
    135deg,
    rgba(59, 130, 246, 0.06),
    rgba(15, 23, 42, 0.01)
  );
  border-radius: 24px;
  border: 1px solid rgba(148, 163, 184, 0.35);
}

/* Encabezado de lista de productos */
.products-header h2 {
  letter-spacing: 0.01em;
}
</style>
